@charset "UTF-8";
/*Endast importer av partials*/
* {
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  min-height: 100vh;
  font-family: "Zen Maru Gothic", "Open Sans", sans-serif;
}

header {
  max-width: 600px;
  width: 90%;
  margin: 0 auto;
  padding-top: 2em;
}
header h1 {
  font-family: "Indie Flower", Georgia, "Times New Roman", Times, serif;
  letter-spacing: 1px;
  color: #333;
}

main {
  max-width: 600px;
  width: 90%;
  margin: 0 auto;
  box-shadow: 7.5px 7.5px #eee;
  border: 2px solid #ddd;
  min-height: 300px;
  box-sizing: border-box;
  padding: 3em 2em;
}
main p {
  font-family: "Zen Maru Gothic", "Open Sans", sans-serif;
  font-size: 1em;
  line-height: 1.5em;
}

aside {
  height: 100px;
}

footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #ddd;
  text-align: right;
  padding: 0.5em;
}

.coursesList {
  list-style: none;
}
.coursesList li {
  padding: 1em;
  padding-left: 0;
  height: 30px;
}
.coursesList li:hover {
  background-color: #eee;
}
.coursesList li:before {
  content: "ðŸœ²";
}
.coursesList button {
  float: right;
  width: 30px;
  height: 30px;
  border: 1px solid white;
  border-radius: 0.5em;
  background-color: white;
  align-items: center;
  vertical-align: middle;
}
.coursesList button:hover {
  cursor: pointer;
  transform: scale(0.9);
}
.coursesList button img {
  width: 100%;
  height: 100%;
}

.message, .success, .error {
  padding: 0.3em;
  font-weight: bold;
}

.error {
  background-color: #e25353;
}

.success {
  background-color: #e25353;
}